	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by noboru-i","subtitle":"noboru-iのメモ的な何かです。","copyright":"© All rights reserved.","author":{"name":"noboru-i","twitter":"noboru_i"},"disqusShortname":"","url":"https://noboru-i.github.io/"}},"markdownRemark":{"id":"/home/circleci/code/src/pages/articles/2018/2018-06-22.md absPath of file >>> MarkdownRemark","html":"<h2>「Dockerによるアプリケーション開発環境構築ガイド」はプロのエンジニア必読の一冊だ（断言） - Lean Baseball</h2>\n<p><a href=\"http://shinyorke.hatenablog.com/entry/docker\">http://shinyorke.hatenablog.com/entry/docker</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/docker/\">Docker</a>\n  </li>\n</ul>\n<p>Docker本の紹介。</p>\n<p>最近、Dockerをちょっと触ったけど、なんとなく使うことはできても、いまいち理解しきれてない感じなので、そのうち読んで見る。</p>\n<h2>Introducing Zeplin 2.0 — Components and a ton more goodies</h2>\n<p><a href=\"https://blog.zeplin.io/introducing-zeplin-2-0-components-and-a-ton-more-goodies-7c09dacc1f48\">https://blog.zeplin.io/introducing-zeplin-2-0-components-and-a-ton-more-goodies-7c09dacc1f48</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/design/\">Design</a>\n  </li>\n</ul>\n<p>Zeplinがバージョン2になって、コンポーネントベースでのデザインとの親和性が高くなった。</p>\n<p>貼ってあるgifアニメを見たら、\n・カンプからコンポーネントへの遷移\n・コンポーネントから、利用されているカンプがわかる\nとか、良さげ。</p>\n<h2>いつ ReactNative を使っても大丈夫か - mizchi’s blog</h2>\n<p><a href=\"http://mizchi.hatenablog.com/entry/2018/06/20/115539\">http://mizchi.hatenablog.com/entry/2018/06/20/115539</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/react native/\">React Native</a>\n  </li>\n</ul>\n<p>mizchiさんの考える、ReactNative採用可否フローチャート。</p>\n<p>Expo未経験なので、それでどこまで楽になるんだろう？というのは未知数ですが、全体的には同意。</p>\n<p>極論、Webサイトをそのままアプリ化するのであればRNを使えそう、という域を超えない印象。\n（アプリらしさとかを考えると辛そう、みたいな。）</p>\n<h2>iOSの11.3からのPWA対応でできるようになったこと – Takeshi Amano – Medium</h2>\n<p><a href=\"https://medium.com/@takeshiamano/ios%E3%81%AE11-3%E3%81%8B%E3%82%89%E3%81%AEpwa%E5%AF%BE%E5%BF%9C%E3%81%A7%E3%81%A7%E3%81%8D%E3%82%8B%E3%82%88%E3%81%86%E3%81%AB%E3%81%AA%E3%81%A3%E3%81%9F%E3%81%93%E3%81%A8-313f638a172b\">https://medium.com/@takeshiamano/ios%E3%81%AE11-3%E3%81%8B%E3%82%89%E3%81%AEpwa%E5%AF%BE%E5%BF%9C%E3%81%A7%E3%81%A7%E3%81%8D%E3%82%8B%E3%82%88%E3%81%86%E3%81%AB%E3%81%AA%E3%81%A3%E3%81%9F%E3%81%93%E3%81%A8-313f638a172b</a></p>\n<ul class=\"post-single__tags-list\">\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/pwa/\">PWA</a>\n  </li>\n  <li class=\"post-single__tags-list-item\">\n    <a class=\"post-single__tags-list-item-link\" href=\"/tags/ios/\">iOS</a>\n  </li>\n</ul>\n<p>最新のiOSにて、PWA的な出来ることのまとめ。</p>\n<p>iOSとAndroidでの差も書いてあり、実装時には一通り確認したほうが良さそう。</p>","fields":{"tagSlugs":["/tags/docker/","/tags/design/","/tags/react-native/","/tags/pwa/","/tags/i-os/"]},"frontmatter":{"title":"Weekly news at 2018/06/22","tags":["Docker","Design","React Native","PWA","iOS"],"date":"2018-06-22T17:01:03+00:00","description":"2018/06/22のウィークリーニュース"}}},"pathContext":{"slug":"/posts/weekly-news/2018/06/22/"}}

